import { Injectable } from '@angular/core';
import { ServerData } from '../models/server-data.model';
import { Observable } from 'rxjs/Observable';
import { Subscriber } from 'rxjs/Subscriber';

/**
 * 
 */
export interface Extra {
	type: number, // 16161GPS66
	gpsAccuracyStatus: number, // GPS0GPS1GPS2GPS3GPS
	addr: string // 
}

export interface Location {
	watchId?: number,
	position: Position,
	extra: Extra
}

@Injectable()
export class GeolocationService {

	constructor() {
	}

	/**
	 * 
	 * @param {PositionOptions} positionOptions
	 * @returns {Observable<ServerData>}
	 */
	getCurrentPosition(positionOptions: PositionOptions): Observable<ServerData> {
		return new Observable<ServerData>((subscriber: Subscriber<ServerData>) => {
			navigator.geolocation.getCurrentPosition((...args: any[]) => {
				const position: Position = args[0],
					extra: Extra = args[1],
					location: Location = {
						position: position,
						extra: extra
					};
				subscriber.next({
					code: 'ok',
					info: '',
					result: location
				});
			}, (error: PositionError) => {
				subscriber.error({
					code: 'error',
					info: '',
					result: error
				});
			}, positionOptions);
		});
	}

	/**
	 * 
	 * @param {PositionOptions} positionOptions
	 * @returns {Observable<ServerData>}
	 */
	watchPosition(positionOptions: PositionOptions): Observable<ServerData> {
		return new Observable<ServerData>((subscriber: Subscriber<ServerData>) => {
			const watchId: number = navigator.geolocation.watchPosition((...args: any[]) => {
				const position: Position = args[0],
					extra: any = args[1],
					location: Location = {
						watchId: watchId,
						position: position,
						extra: extra
					};
				subscriber.next({
					code: 'ok',
					info: '',
					result: location
				});
			}, (error: PositionError) => {
				subscriber.error({
					code: 'error',
					info: '',
					result: error
				});
				subscriber.complete();
			}, positionOptions);
		});
	}

	/**
	 * 
	 * @param {number} watchId id
	 */
	clearWatch(watchId: number): void {
		navigator.geolocation.clearWatch(watchId);
	}
}



