import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { AuthService } from './auth.service';
import { ConfigService } from './config.service';
import { TipsService } from './tips.service';
import { Alert, AlertOptions, App, Loading } from 'ionic-angular';
import { LoginPage } from '../pages/login/login';
import { Observable } from 'rxjs/Observable';
import { Subscriber } from 'rxjs/Subscriber';
import { ServerData } from '../models/server-data.model';

@Injectable()
export class RequestService {
	alertAlert: Alert = null;

	constructor(private http: HttpClient,
				private appCtrl: App,
				private tips: TipsService,
				private authService: AuthService,
				private config: ConfigService) {
	}

	/**
	 * post
	 * @param {string} url 
	 * @param {*} body body
	 * @param {boolean} [showLoader=true] 
	 * @param {boolean} [isMock=false] 
	 * @returns {Observable<any>}
	 */
	post(url: string, body: any, showLoader: boolean = true, isMock: boolean = false): Observable<ServerData> {
		let loader: Loading;
		if (showLoader) {
			loader = this.tips.loader();
		}
		const headers = new HttpHeaders({
				'Content-Type': 'application/json',
				'URMS_LOGIN_TOKEN': this.authService.token || ''
			}),
			options = {headers: headers},
			requesUrl: string = (isMock ? this.config.mockDomain : this.config.domain) + url;
		return new Observable<ServerData>((subscriber: Subscriber<any>) => {
			this.http.post(requesUrl, body && JSON.stringify(body), options).subscribe((serverData: ServerData) => {
				const id = setTimeout(() => {
					clearTimeout(id);
					this.tips.dismiss(loader);
				});
				subscriber.next(serverData);
				subscriber.complete();
			}, (error: HttpErrorResponse) => {
				const id = setTimeout(() => {
					clearTimeout(id);
					this.tips.dismiss(loader);
				});
				this.handlerError('post', error, url, body, showLoader, isMock, subscriber);
			});
		});
	}

	/**
	 * get
	 * @param {string} url 
	 * @param {boolean} [showLoader=true] 
	 * @param {boolean} [isMock=false] 
	 * @returns {Observable<any>}
	 */
	get(url: string, showLoader: boolean = true, isMock: boolean = false): Observable<any> {
		let loader: Loading;
		if (showLoader) {
			loader = this.tips.loader();
		}
		const headers = new HttpHeaders({
				'Content-Type': 'application/json',
				'URMS_LOGIN_TOKEN': this.authService.token || ''
			}),
			options = {headers: headers},
			requesUrl: string = (isMock ? this.config.mockDomain : this.config.domain) + url;
		return new Observable<ServerData>((subscriber: Subscriber<any>) => {
			this.http.get(requesUrl, options).subscribe((serverData: ServerData) => {
				const id = setTimeout(() => {
					clearTimeout(id);
					this.tips.dismiss(loader);
				});
				subscriber.next(serverData);
				subscriber.complete();
			}, (error: HttpErrorResponse) => {
				const id = setTimeout(() => {
					clearTimeout(id);
					this.tips.dismiss(loader);
				});
				this.handlerError('get', error, url, null, showLoader, isMock, subscriber);
			});
		});
	}

	/**
	 * delete
	 * @param {string} url 
	 * @param {boolean} [showLoader=true] 
	 * @param {boolean} [isMock=false] 
	 * @returns {Observable<any>}
	 */
	delete(url: string, showLoader: boolean = true, isMock: boolean = false): Observable<any> {
		let loader: Loading;
		if (showLoader) {
			loader = this.tips.loader();
		}
		const headers = new HttpHeaders({
				'Content-Type': 'application/json',
				'URMS_LOGIN_TOKEN': this.authService.token || ''
			}),
			options = {headers: headers},
			requesUrl: string = (isMock ? this.config.mockDomain : this.config.domain) + url;
		return new Observable<ServerData>((subscriber: Subscriber<any>) => {
			this.http.delete(requesUrl, options).subscribe((serverData: ServerData) => {
				const id = setTimeout(() => {
					clearTimeout(id);
					this.tips.dismiss(loader);
				});
				subscriber.next(serverData);
				subscriber.complete();
			}, (error: HttpErrorResponse) => {
				const id = setTimeout(() => {
					clearTimeout(id);
					this.tips.dismiss(loader);
				});
				this.handlerError('delete', error, url, null, showLoader, isMock, subscriber);
			});
		});
	}

	/**
	 * 
	 * @param {string} type post or get
	 * @param {HttpErrorResponse} error 
	 * @param {string} url 
	 * @param {any} obj body
	 * @param {boolean} showLoader 
	 * @param {boolean} isMock 
	 * @param {Subscriber} subscriber 
	 */
	private handlerError(type: string, error: HttpErrorResponse, url: string, obj?: any, showLoader?: boolean, isMock?: boolean, subscriber?: Subscriber<any>) {
		let alertOptions: AlertOptions = null;
		if (error.status === 0) {
			alertOptions = {
				title: '',
				message: '',
				buttons: ['']
			};
		} else if (error.status === 401) {
			alertOptions = {
				title: '',
				message: '',
				buttons: [{
					text: '',
					handler: () => {
						this.authService.removeToken().then(() => {
							this.appCtrl.getRootNav().setRoot(LoginPage);
						});
					}
				}]
			};
		} else if (error.status === 404) {
			alertOptions = {
				title: '',
				message: '',
				buttons: ['']
			};
			if (!isMock) { // 
				if (this.config.isDev || this.config.isTest) { // 
					isMock = true;
					if (type === 'post') {
						this.post(url, obj, showLoader, isMock).subscribe((data: ServerData) => {
							subscriber.next(data);
							subscriber.complete();
						});
					} else if (type === 'get') {
						this.get(url, showLoader, isMock).subscribe((data: ServerData) => {
							subscriber.next(data);
							subscriber.complete();
						});
					} else if (type === 'delete') {
						this.delete(url, showLoader, isMock).subscribe((data: ServerData) => {
							subscriber.next(data);
							subscriber.complete();
						});
					}
					return;
				}
			}
		} else {
			alertOptions = {
				title: '',
				message: error.message || 'Server Error',
				buttons: ['']
			};
		}
		if (this.alertAlert) {
			this.alertAlert.dismiss().then(() => {
				this.alertAlert = this.tips.alert(alertOptions);
			});
		} else {
			this.alertAlert = this.tips.alert(alertOptions);
		}
		subscriber.error(error);
		subscriber.complete();
		throw error;
	}
}



